import { messageHandler } from '@estruyf/vscode/dist/client';
import { WebviewMessage } from '../../shared/types';
import { deployedContracts } from '../stores/sakeStore';
export function copyToClipboard(stringToCopy) {
    messageHandler.send(WebviewMessage.copyToClipboard, stringToCopy);
}
export async function setBalance(address, balance) {
    const _params = {
        balances: {
            [address]: balance
        }
    };
    const success = await messageHandler.request(WebviewMessage.onSetBalances, _params);
    return success;
}
export function functionCall(payload) {
    messageHandler.send(WebviewMessage.onContractFunctionCall, payload);
}
export function deployContract(contractFqn, sender, calldata, value = 0) {
    const _pamars = {
        contractFqn: contractFqn,
        sender: sender,
        calldata: calldata,
        value: value
    };
    messageHandler.send(WebviewMessage.onDeploy, _pamars);
}
export function showErrorMessage(message) {
    messageHandler.send(WebviewMessage.onError, message);
}
export function showInfoMessage(message) {
    messageHandler.send(WebviewMessage.onInfo, message);
}
export async function getInputFromTopBar(value = '', title = undefined) {
    return await messageHandler.request(WebviewMessage.getTextFromInputBox, {
        value: value,
        title: title
    });
}
export async function compileContracts() {
    return await messageHandler.request(WebviewMessage.onCompile);
}
export async function removeContract(contract) {
    return await messageHandler.request(WebviewMessage.onUndeployContract, contract);
    // deployedContracts.update((state) => {
    //     return state.filter((c) => c.address !== contract.address);
    // });
}
export async function setLabel(contract) {
    return await messageHandler.request(WebviewMessage.onsetLabel, contract);
}
export async function navigateTo(path, startOffset, endOffset) {
    messageHandler.send(WebviewMessage.onNavigate, { path, startOffset, endOffset });
}
export async function openExternal(url) {
    messageHandler.send(WebviewMessage.onOpenExternal, { path: url });
}
