import { writable, get, derived } from 'svelte/store';
import { parseComplexNumber } from '../../shared/validate';
/**
 * App Stores
 */
export const selectedAccount = writable(null);
export const selectedValueString = writable(null);
// null indicated wrong stirng input
export const selectedValue = derived(selectedValueString, ($selectedValueString) => {
    if ($selectedValueString === null || $selectedValueString === '') {
        return 0;
    }
    try {
        return parseComplexNumber($selectedValueString);
    }
    catch (e) {
        return null;
    }
});
export const compilationIssuesVisible = writable(false);
export const activeTabId = writable();
export const txParametersExpanded = writable(true);
