<script lang="ts">
    import {
        provideVSCodeDesignSystem,
        vsCodeButton,
        vsCodeDropdown,
        vsCodeOption,
        vsCodeDivider,
        vsCodeCheckbox,
        vsCodeTextField
    } from '@vscode/webview-ui-toolkit';
    import { type CompiledContract } from '../../shared/types';
    import Constructor from '../components/Constructor.svelte';
    import { compilationState } from '../stores/sakeStore';
    import { selectedAccount, selectedValue } from '../stores/appStore';
    import { deployContract, showErrorMessage } from '../helpers/api';
    import TextContainerDark from '../components/TextContainerDark.svelte';

    provideVSCodeDesignSystem().register(
        vsCodeButton(),
        vsCodeDropdown(),
        vsCodeOption(),
        vsCodeDivider(),
        vsCodeCheckbox(),
        vsCodeTextField()
    );

    let filterString: string = '';
    $: filteredContracts = $compilationState.contracts.filter((contract) =>
        contract.fqn.toLowerCase().includes(filterString.toLowerCase())
    );

    const deploy = async function (contract: CompiledContract, calldata: string) {
        const _sender: string | undefined = $selectedAccount?.address;
        if (_sender === undefined) {
            showErrorMessage('Failed deployment, undefined sender');
            return;
        }

        const _value = $selectedValue ?? 0;

        deployContract(contract.fqn, _sender, calldata, _value);
    };

    const handleFilter = function (e: any) {
        filterString = e.target?.value;
    };
</script>

{#if $compilationState.contracts.length > 0 || $compilationState.issues.length > 0}
    <section class="p-3">
        <!-- <section class="w-full flex-grow overflow-y-scroll"> -->
        <vscode-text-field
            class="w-full mb-2"
            placeholder="Filter compiled contracts"
            disabled={$compilationState.contracts.length === 0}
            value={filterString}
            on:input={handleFilter}
        >
            <span slot="start">
                <svg
                    width="14"
                    height="14"
                    viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                    ><path
                        d="M15.25 0a8.25 8.25 0 0 0-6.18 13.72L1 22.88l1.12 1 8.05-9.12A8.251 8.251 0 1 0 15.25.01V0zm0 15a6.75 6.75 0 1 1 0-13.5 6.75 6.75 0 0 1 0 13.5z"
                    /></svg
                >
            </span>
        </vscode-text-field>

        <div class="flex flex-col gap-1">
            {#if filteredContracts.length === 0}
                <TextContainerDark>No contracts found</TextContainerDark>
            {/if}

            {#each filteredContracts as contract (contract.fqn)}
                <Constructor
                    abi={contract.abi}
                    name={contract.name}
                    onDeploy={(calldata) => deploy(contract, calldata)}
                />
            {/each}
        </div>
    </section>
    <!-- </section> -->
{:else}
    <!-- <section class="h-full w-full flex flex-col items-center justify-center gap-3"> -->
    <div
        class="flex flex-col gap-2 items-center text-vscodeInputForeground h-full justify-center p-2"
    >
        <svg
            width="16"
            height="16"
            viewBox="0 0 16 16"
            xmlns="http://www.w3.org/2000/svg"
            fill="currentColor"
            ><path
                d="M2.807 14.975a1.75 1.75 0 0 1-1.255-.556 1.684 1.684 0 0 1-.544-1.1A1.72 1.72 0 0 1 1.36 12.1c1.208-1.27 3.587-3.65 5.318-5.345a4.257 4.257 0 0 1 .048-3.078 4.095 4.095 0 0 1 1.665-1.969 4.259 4.259 0 0 1 4.04-.36l.617.268-2.866 2.951 1.255 1.259 2.944-2.877.267.619a4.295 4.295 0 0 1 .04 3.311 4.198 4.198 0 0 1-.923 1.392 4.27 4.27 0 0 1-.743.581 4.217 4.217 0 0 1-3.812.446c-1.098 1.112-3.84 3.872-5.32 5.254a1.63 1.63 0 0 1-1.084.423zm7.938-13.047a3.32 3.32 0 0 0-1.849.557c-.213.13-.412.284-.591.458a3.321 3.321 0 0 0-.657 3.733l.135.297-.233.227c-1.738 1.697-4.269 4.22-5.485 5.504a.805.805 0 0 0 .132 1.05.911.911 0 0 0 .298.22c.1.044.209.069.319.072a.694.694 0 0 0 .45-.181c1.573-1.469 4.612-4.539 5.504-5.44l.23-.232.294.135a3.286 3.286 0 0 0 3.225-.254 3.33 3.33 0 0 0 .591-.464 3.28 3.28 0 0 0 .964-2.358c0-.215-.021-.43-.064-.642L11.43 7.125 8.879 4.578l2.515-2.59a3.286 3.286 0 0 0-.65-.06z"
            /></svg
        >
        <span class="text-sm my-2 text-center text-secon">No compiled contracts </span>
    </div>
    <!-- </section> -->
    <!-- <p class="text-center">No contracts are compiled</p> -->
{/if}
